.line{
 
   height:0.3rem;
   width:16rem;
   background-color:var(--primary-color);
   margin: 1rem auto;
   position: relative;
}
.line:after, .line:before{
    position: absolute;
    content:"";
    width:1.5rem;
    height: 1.5rem;
    background-color:transparent;
    border:3px solid var(--primary-color);
    transform: rotate(45deg);
}
.line::before{
left:-1.5rem;
bottom: -0.6rem;
}
.line::after{
    right:-1.5rem;
    top: -0.6rem;
}
