#portfolio h1 {
  font-size: 3.5rem;
}
.project {
  margin: 7rem auto 3rem auto;
  position: relative;
  overflow: hidden;
  color: var(--black);
  text-align: center;
  perspective: 50em;
}
.project * {
  box-sizing: padding-box;

  transition: all 0.2s ease-out;
}
.project img {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  max-width: 100%;
  min-height: 100%;
  background-color: var(--white);
  vertical-align: top;
}
.project-caption {
  top: 50%;
  left: 20px;
  right: 20px;
  position: absolute;
  opacity: 0;
  z-index: 1;
}
.project h2,
h4 {
  color: var(--primary-color);
}
.project:after {
  background-color: var(--black);
  position: absolute;
  content: "";
  display: block;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;

  transition: all 0.4s ease-in-out;

  transform: rotateX(-90deg);

  transform-origin: 50% 50%;
  opacity: 0;
}

.project:hover .project-caption {
  transform: translateY(-50%);
  opacity: 1;

  transition-delay: 0.2s;
}
.project:hover:after {
  transform: rotateX(0);
  opacity: 0.7;
}
.project-caption .btn {
  width: 200px;
}
.project-caption h3{
  color: var(--white)
}

