:root{
--main-font: 'Libre Baskerville', serif;
--sub-font:'Montserrat', sans-serif;
--white:#fff;
--black:#000;
--primary-color:#862121;
--dark:rgba(0, 0, 0, 0.7);
--content-color:#5f5f5f;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    transition: all 0.3s ease-in-out;
    scroll-behavior: smooth;
   

}
html{
    font-size: 62.5%;
    outline: none;
    border: none;
    overflow-x: hidden;
   
    background-color: #fff;
   
    background-color: var(--white);
}
a, a:hover{
    text-decoration: none;
    color: #fff;
    color: var(--white);
}

h1,h3,h4{
    font-weight:700;
}
h1{
    font-family: 'Libre Baskerville', serif;
    font-family: var(--main-font);
    color: #fff;
    color: var(--white);
    line-height:1.7;
    text-transform: capitalize;
}
h2{
    color: #000;
    color: var(--black);
    font-size: 2.8rem;
    font-weight: 500;
  
}
h3,h4{
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--sub-font);
    letter-spacing: 5px;
    color: #862121;
    color: var(--primary-color);
}
h4{
    letter-spacing:3px;
 font-size:2.3rem;

}
h5{
    font-family: 'Libre Baskerville', serif;
    font-family: var(--main-font);
    font-size:2.4rem; 
    font-weight: 600;
}
p{
    font-family: 'Montserrat', sans-serif;
    font-family: var(--sub-font);
    color: #5f5f5f;
    color: var(--content-color);
    font-size: 1.8rem;
    line-height:1.8;
}
.p-top{
padding-top: 5rem;
}
.underline{
    position: absolute;
    width: 55px;
    height: 3px;
   background-color: #862121;
   background-color: var(--primary-color);
}

/* width */
::-webkit-scrollbar {
    width: 1rem;
    background-color: #e4e4e4;
  }
  /* Track */
  ::-webkit-scrollbar-track {
    border-radius: 10px;
  }
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #000;
    background: var(--black); 
    border-radius: 10px;
  }
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 0, 0, 0.7);
    background: var(--dark); 
  }
  @media (max-width: 580px){
    html{
        font-size:50%;
    }  
    
}
  
